.auth {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  gap: 4rem;
  position: relative;
}
.auth > div {
  display: flex;
  align-items: center;
  justify-content: center;
}

/* ------left side ------------------- */

.authLeft {
  gap: 2rem;
}
.authLeft > svg {
  width: 4rem;
  height: 4rem;
}
.webName > h1 {
  color: #45ecb4;
}
.webName > h6 {
  font-size: 0.85rem;
}
.infoForm {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}
.infoInput {
  border: none;
  outline: none;
  color: ghostwhite;
  font-size: 1.1rem;
  background-color: rgb(49, 49, 49);
  border-radius: 8px;
  padding: 20px;
  flex: 1;
}
.infoForm > div {
  display: flex;
  gap: 1rem;
  height: 2rem;
  width: 90%;
}
.infoButton {
  width: 6rem;
  height: 2rem;
  align-self: flex-end;
}
.authForm {
  background-color: #f6f5f58c;
  border-radius: 1rem;
  padding: 1rem;
}
.authForm > div {
  width: 100%;
  align-items: center;
  justify-content: center;
}
